
import SVG from 'react-inlinesvg';
import { baseUrl } from "../../../../../Assets/AssetsHelper"
const CollectionButton = ({ addCollectionModal }) => {
    return <> <button type="button" className="card rounded-1 card-custom mb-sm-0 text-decoration-none w-100 p-0" onClick={addCollectionModal}>
        <div className="card-body d-flex flex-column p-2">
            <div className="d-flex align-items-center">
                <div className="btn btn-link text-decoration-none bg-light btn-icon flex-shrink-0 h-48px w-48px me-3 block-center">
                    <span className="svg-icon svg-icon-3 me-0">
                        <SVG src={baseUrl('media/icons/svg-icons/add-thin-line.svg')} height={20} width={20} />
                    </span>
                </div>
                <div className="overflow-hidden text-start">
                    <p className="fs-7 fw-medium mb-1 text-gray-800 text-ellipsis">Create Collections</p>
                    <p className="mb-0 text-gray-600 text-ellipsis">0 Assets</p>
                </div>
            </div>
        </div>
    </button></>
}
export default CollectionButton